body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#root {
  width: 60%;
  margin: 0 auto;
}
@media screen and (max-width: 600px) {
  #map {
    display: none;
  }
  #nav-bar.responsive {
    position: relative;
  }
  #nav-bar.responsive .icon {
    position: absolute;
    right: 0;
    top: 0;
  }
  #nav-bar.responsive a {
    float: none;
    display: block;
    text-align: left;
  }
  #nav-bar > a:not(:first-child) {
    display: none;
  }
  #nav-bar a.icon {
    float: right;
    display: block;
  }
}

.App {
  text-align: center;
}
.menu {
  float: right;
  overflow: hidden;
}
.menu .dropbtn {
  font-size: 24px;
  border: none;
  outline: none;
  color: white;
  font-weight: bold;
  background-color: inherit;
  font-family: "Comic Sans MS", sans-serif;
  margin: 0;
}
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #2a2424;
  min-width: 14px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.menu:hover .dropdown-content {
  display: block;
}
#nav-bar {
  display: flex;
  position: fixed;
  right: 0px;
  top: 0px;
  justify-content: space-evenly;
  width: 100%;
  padding: 5px 0px 10px 5px;
  background-color: #2a2424;
}
.nav-link {
  font-family: "Comic Sans MS", sans-serif;
  font-weight: bold;
  font-size: 24px;
  color: white;
  text-decoration: none;
}
@media screen and (device-width: 360px) and (device-height: 640px) and (-webkit-device-pixel-ratio: 6) and (orientation: landscape) {
  p {
    padding: 10%;
  }
  #nav-bar {
    display: flex;
  }
  body {
  }
}

p {
  margin: 5%;
}
h1 {
  padding: 25px;
  font-size: 50px;
}
body {
  background-color: firebrick;
  font-family: "Comic Sans MS", sans-serif;
}

h2 {
  padding-top: 5%;
}

#home {
  width: auto;
  margin: 15px auto;
  margin-top: 65px;
  display: block;
}
#about {
  display: flex;
}

